.card--course {
    .wrapper--card__label__details {
        float: right; // Makes styles more manageable; avoids text overlap on card heading
        display: flex;
        flex-direction: column;

        @include media('<tablet') {
            display: flex;
            flex-direction: column;
        }
        .card--course__label--clearing {
            float: right;

            p {
                @include smalltype();

                background-color: $color-yellow40;
                font-weight: $font-weight-bold;
                padding: $measure--threequarters;
                margin: $measure--basic;

                @include media('<tablet') {
                    margin: $measure--basic 0 $measure--basic $measure--basic;
                }

                @include media('<mobile') {
                    margin: 0;
                }

                @include media('<mobile') {
                    position: absolute;
                }
            }
        }

        .card--course__label--citystgeorges {
            // margin-bottom: $measure--small5;
            display: flex;
            flex-direction: row-reverse;

            p {
                @include smalltype();
                background-color: $color-zima-blue;
                color: $color-purewhite;
                font-weight: $font-weight-bold;
                margin-top: 0;
                padding: $measure--threequarters;
                //position: absolute;
                //right: 0;
                //top: 0;
            }
        }
    }

    // Add a margin to allow for spacing between clearing label and card text that follows
    .card--course__label--clearing + .card__details__text {
        @include media('<mobile') {
            margin-top: $measure--medium3;
        }
    }
}
